import React, { Component } from "react";
import { List, message, Avatar, Spin } from "antd";
import InfiniteScroll from "react-infinite-scroller";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import IndexHeader from "components/Headers/IndexHeader.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import firebase from "firebase";
import { connect } from "react-redux";
import UserDetails from "views/examples/UserDetails";
import { Link } from "react-router-dom";
import "antd/dist/antd.css";

const arr = [];
class Logs extends Component {
  state = {
    data: [],
    uid: null,
    loading: false,
    hasMore: true,
    lat: null,
    long: null
  };

  componentDidMount() {
    this.fetchData(res => {
      this.setState({
        data: arr
      });
    });
  }

  fetchData = callback => {
    const { auth } = this.props;
    const db = firebase.firestore();
    let randomHash = auth.uid;
    db.collection("Child")
      .doc("ChildAnalytics")
      .collection("PointLogs")
      .doc(this.props.match.params.id)
      .collection("Points")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          arr.push(doc.data());
          callback(arr);
        });
      });
  };

  render() {
    return (
      <>
        <IndexNavbar />
        <div className="wrapper">
          <br />
          <br />
          <div className="container white listContainer">
            <div className="demo-infinite-container">
              <InfiniteScroll
                initialLoad={false}
                pageStart={0}
                loadMore={this.handleInfiniteOnLoad}
                hasMore={!this.state.loading && this.state.hasMore}
                useWindow={false}
              >
                <List
                  dataSource={this.state.data}
                  renderItem={item => (
                    <List.Item key={item.ParentID}>
                      <List.Item.Meta
                        avatar={
                          <Avatar src="https://zos.http://pluspng.com/img-png/user-png-icon-user-2-icon-png-file-512x512-pixel-512.png.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                        }
                        title={
                          <a
                            target="_blank"
                            href={`https://maps.google.com/?q=${this.state.lat},${this.state.long}`}
                          >
                            {item.Location}
                          </a>
                        }
                        description={(item.Email, item.PhoneNumber)}
                      />
                    </List.Item>
                  )}
                >
                  {this.state.loading && this.state.hasMore && (
                    <div className="demo-loading-container">
                      <Spin />
                    </div>
                  )}
                </List>
              </InfiniteScroll>
            </div>
          </div>
          <DarkFooter />
        </div>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.firebase.auth
  };
};

export default connect(mapStateToProps)(Logs);
