import React, { Component } from "react";
import { List, message, Avatar, Spin } from "antd";
import InfiniteScroll from "react-infinite-scroller";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import IndexHeader from "components/Headers/IndexHeader.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import firebase from "firebase";
import { connect } from "react-redux";
import "antd/dist/antd.css";

import { Card, CardBody, Form } from "reactstrap";

const arr = [];
class Childdetails extends Component {
  state = {
    data: [],
    uid: null,
    loading: false,
    hasMore: true,
    lat: null,
    long: null
  };

  componentDidMount() {
    this.fetchData(res => {
      this.setState({
        data: arr
      });
    });
  }

  fetchData = callback => {
    console.log();

    const { auth } = this.props;
    const db = firebase.firestore();
    let randomHash = auth.uid;
    db.collection("Child")
      .doc("ChildIDs")
      .collection(this.props.match.params.id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(docs => {
          arr.push(docs.data());
          console.log(docs.id, docs.data());
          callback(arr);
        });
      });
  };

  render() {
    return (
      <>
        <IndexNavbar />
        <br />
        <br />
        <br />
        <br />

        {/* <IndexHeader /> */}
        <div
          style={{
            backgroundSize: "cover",
            backgroundImage:
              "url(" + require("assets/img/register-bg.png") + ")"
          }}
        >
          {this.state.data.map(function(item, index) {
            console.log();
            return (
              <div align="center">
                <br />
                <br />
                <br />
                <br />
                <br />
                <Card className="card-signup">
                  <Form action="" className="form" method="">
                    <CardBody>
                      <span>&nbsp;</span>
                      <h3> Driver Details: </h3>
                      <p>User ID: {item.UserID}</p>
                      <p>Name: {item.Name}</p>
                      <p>Email: {item.Email}</p>
                      <p>Date of Birth: {item.DateOfBirth}</p>
                      <p>Parent ID: {item.ParentFID}</p>
                      <p>Signup Date: {item.SignupDate}</p>
                      <span>&nbsp;</span>
                    </CardBody>
                  </Form>
                </Card>
              </div>
            );
          })}
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />

          <DarkFooter />
        </div>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.firebase.auth
  };
};

export default connect(mapStateToProps)(Childdetails);
