import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

// styles for this kit
import "assets/css/bootstrap.min.css";
import "assets/scss/now-ui-kit.scss";
import "assets/demo/demo.css";
import "assets/demo/nucleo-icons-page-styles.css";
// pages for this kit
import Index from "views/Index.js";
import NucleoIcons from "views/NucleoIcons.js";
import LoginPage from "views/examples/LoginPage.js";
import LandingPage from "views/examples/LandingPage.js";
import ProfilePage from "views/examples/ProfilePage.js";
import Childlist from "views/examples/ChildList";
import UserDetails from "views/examples/UserDetails";
import { createStore, applyMiddleware, compose } from "redux";
import rootReducer from "./store/reducers/rootReducer";
import { Provider } from "react-redux";
import thunk from "redux-thunk";
import {
  createFirestoreInstance,
  getFirestore,
  reduxFirestore
} from "redux-firestore";
import { ReactReduxFirebaseProvider, getFirebase } from "react-redux-firebase";
import fbConfig from "./config/Fire";
import firebase from "firebase/app";
import ParentList from "views/examples/ParentList";
import MapContainer from "views/examples/Map";
import Logs from "views/examples/Logs";
import Childdetails from "./views/examples/Childdetails";
import MapAllUser from "./views/examples/allUser";

const store = createStore(
  rootReducer,
  compose(
    applyMiddleware(thunk.withExtraArgument({ getFirestore, getFirebase })),
    reduxFirestore(firebase, fbConfig)
  )
);

const rrfProps = {
  firebase,
  config: fbConfig,
  dispatch: store.dispatch,
  createFirestoreInstance
};

ReactDOM.render(
  <Provider store={store}>
    <ReactReduxFirebaseProvider {...rrfProps}>
      <BrowserRouter>
        <Switch>
          <Switch>
            <Route path="/index" render={props => <Index {...props} />} />
            <Route
              path="/nucleo-icons"
              render={props => <NucleoIcons {...props} />}
            />
            <Route
              path="/landing-page"
              render={props => <LandingPage {...props} />}
            />
            <Route
              path="/profile-page"
              render={props => <ProfilePage {...props} />}
            />
            <Route
              path="/login-page"
              render={props => <LoginPage {...props} />}
            />
            <Route
              path="/child-list"
              render={props => <Childlist {...props} />}
            />
            <Route
              path="/parent-list"
              render={props => <ParentList {...props} />}
            />
            <Route
              path="/user-detail/:id"
              render={props => <UserDetails {...props} />}
            />
            <Route
              path="/driversmap/:id"
              render={props => <MapContainer {...props} />}
            />
            <Route
              path="/childdetails/:id"
              render={props => <Childdetails {...props} />}
            />
            <Route
              path="/allusers/"
              render={props => <MapAllUser {...props} />}
            />
            <Route path="/logs/:id" render={props => <Logs {...props} />} />
            <Route path="/logs/:id" render={props => <Logs {...props} />} />
            <Redirect to="/index" />
            <Redirect from="/" to="/index" />
          </Switch>
        </Switch>
      </BrowserRouter>
    </ReactReduxFirebaseProvider>
  </Provider>,
  document.getElementById("root")
);
