import React, { Component } from "react";
import {
  withGoogleMap,
  withScriptjs,
  GoogleMap,
  Marker,
  InfoWindow
} from "react-google-maps";
//import * as parkData from "./data/skateboard-parks.json";
import mapStyles from "./mapStyles";
import firebase from "firebase";

const arr = [];
const randomHash = [];
class MapAllUser extends Component {
  state = {
    selectedPark: null,
    data: [],
  };

  componentDidMount() {
    this.fetchData(res => {
      console.log(res);
      this.setState({
        data: arr
      });
    });
  }

  fetchData = callback => {
    const db = firebase.firestore();
    db.collection("Child")
      .doc("ChildLocation")
      .collection("Locations")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          console.log(doc.data());
          arr.push(doc.data());
          callback(arr);
          // db.collection("Parent")
          //   .doc("ParentChild")
          //   .collection(doc.id)
          //   .doc("CH1550")
          //   .collection("Logs")
          //   .get()
          //   .then(querySnapshot => {
          //     querySnapshot.forEach(docs => {
          //       console.log(doc.id, docs.data());
          //       arr.push(docs.data());
          //       callback(arr);
          //     });
          //   });
        });
      });
  };    


  handleSelectedPark = park => {
    if (park) {
      this.setState({
        selectedPark: [park.Latitude, park.Longitude, park.Name]
      });
    } else {
      this.setState({
        selectedPark: null
      });
    }
  };

  render() {
        
    return (
      <div>
        <GoogleMap
          defaultZoom={10}
          defaultCenter={{ lat: 24.860735, lng: 67.001137 }}
          defaultOptions={{ styles: mapStyles }}
        >
          {this.state.data.map((park, index) => (
            <div>
              <Marker
                key={index}
                position={{
                  lat: park.Latitude,
                  lng: park.Longitude
                }}
                onClick={() => {
                  this.handleSelectedPark(park);
                }}
                icon={{
                  scaledSize: new window.google.maps.Size(25, 25)
                }}
              ></Marker>
            </div>
          ))}

          {this.state.selectedPark && (
            <div>
              <InfoWindow
                onCloseClick={() => {
                  this.handleSelectedPark(null);
                }}
                position={{
                  lat: this.state.selectedPark[0],
                  lng: this.state.selectedPark[1]
                }}
              >
                <div>
                  <h2>{this.state.selectedPark[2]}</h2>
                </div>
              </InfoWindow>
            </div>
          )}
        </GoogleMap>
      </div>
    );
  }
}

const MapWrapped = withScriptjs(withGoogleMap(MapAllUser));

export default function App() {
  return (
    <div style={{ width: "100vw", height: "100vh" }}>
      <MapWrapped
        googleMapURL={
          "https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=AIzaSyCiHtJpjO3AiZ2eueyYiRXxPb4xA_MrjAE"
        }
        loadingElement={<div style={{ height: `100%` }} />}
        containerElement={<div style={{ height: `100%` }} />}
        mapElement={<div style={{ height: `100%` }} />}
      />
    </div>
  );
}
