import React, { Component } from "react";
import { List, message, Avatar, Spin } from "antd";
import InfiniteScroll from "react-infinite-scroller";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import IndexHeader from "components/Headers/IndexHeader.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import firebase from "firebase";
import { connect } from "react-redux";
import Childdetails from "../examples/UserDetails";
import "antd/dist/antd.css";
// import dark from "components/Navbars/dark";

const arr = [];
class Childlist extends Component {
  state = {
    data: [],
    uid: null,
    loading: false,
    hasMore: true,
    lat: null,
    long: null
  };
  componentDidMount() {
    // const { auth } = this.props;
    this.fetchData(res => {
      // console.log(res);
      this.setState({
        data: arr
      });
    });
  }

  fetchData = callback => {
    const { auth } = this.props;
    const db = firebase.firestore();
    let randomHash = auth.uid;

    db.collection("UniqueIDs")
      .doc("ParentUnique")
      .collection("ID")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          // console.log(doc.id, doc.data());
          db.collection("Parent")
            .doc("ParentChild")
            .collection(doc.id)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(docs => {
                console.log(doc.id, docs.data());
                arr.push(docs.data());
                callback(arr);
              });
            });
        });
      });
  };

  render() {
    const { auth } = this.props;

    return (
      <>
        <IndexNavbar />
        <div className="wrapper">
          <br />
          <br />
          <br />

          <div className="container white listContainer">
            <div className="demo-infinite-container">
              <InfiniteScroll
                initialLoad={false}
                pageStart={0}
                loadMore={this.handleInfiniteOnLoad}
                hasMore={!this.state.loading && this.state.hasMore}
                useWindow={false}
              >
                <List
                  dataSource={this.state.data}
                  renderItem={item => (
                    <List.Item key={item.UserID}>
                      <List.Item.Meta
                        avatar={
                          <Avatar src="https://zos.http://pluspng.com/img-png/user-png-icon-user-2-icon-png-file-512x512-pixel-512.png.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                        }
                        title={
                          <a
                            target="_blank"
                            href={`https://maps.google.com/?q=${this.state.lat},${this.state.long}`}
                          >
                            {item.UserID}
                          </a>
                        }
                        description={item.Name}
                      />

                      <div>
                        <a href={"/Childdetails/" + item.UserFID}>Details</a>
                      </div>
                      <span> &nbsp; &nbsp; &nbsp; &nbsp;</span>
                      <div>
                        <a href={"/logs/" + item.UserFID}>Logs</a>
                      </div>
                      <span> &nbsp; &nbsp; &nbsp; &nbsp;</span>
                      <div>
                        <a href={"/driversmap/" + item.UserID}>Destinations</a>
                      </div>
                    </List.Item>
                  )}
                >
                  {this.state.loading && this.state.hasMore && (
                    <div className="demo-loading-container">
                      <Spin />
                    </div>
                  )}
                </List>
              </InfiniteScroll>
            </div>
          </div>
          <DarkFooter />
        </div>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.firebase.auth
  };
};

export default connect(mapStateToProps)(Childlist);
