import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { signOut } from "../../store/actions/authActions";

import {
  Button,
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  UncontrolledTooltip
} from "reactstrap";
import { black } from "ansi-colors";

function IndexNavbar(props) {
  const { auth, signOut } = props;

  console.log(auth);
  const [navbarColor, setNavbarColor] = React.useState("navbar-transparent");
  const [collapseOpen, setCollapseOpen] = React.useState(false);

  React.useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 399 ||
        document.body.scrollTop > 399
      ) {
        setNavbarColor("");
      } else if (
        document.documentElement.scrollTop < 400 ||
        document.body.scrollTop < 400
      ) {
        setNavbarColor("navbar-transparent");
      }
    };
    window.addEventListener("scroll", updateNavbarColor);
    return function cleanup() {
      window.removeEventListener("scroll", updateNavbarColor);
    };
  });

  if (auth.uid && auth.email == "admin@admin.com") {
    return (
      <>
        {collapseOpen ? (
          <div
            id="bodyClick"
            onClick={() => {
              document.documentElement.classList.toggle("nav-open");
              setCollapseOpen(false);
            }}
          />
        ) : null}
        <Navbar
          className={"fixed-top " + navbarColor}
          expand="lg"
          color="danger"
        >
          <Container>
            <div className="navbar-translate">
              <NavbarBrand id="navbar-brand" style={{ color: "black" }}>
                PROTECHT
              </NavbarBrand>
              <button
                className="navbar-toggler navbar-toggler"
                onClick={() => {
                  document.documentElement.classList.toggle("nav-open");
                  setCollapseOpen(!collapseOpen);
                }}
                aria-expanded={collapseOpen}
                type="button"
              >
                <span className="navbar-toggler-bar top-bar"></span>
                <span className="navbar-toggler-bar middle-bar"></span>
                <span className="navbar-toggler-bar bottom-bar"></span>
              </button>
            </div>
            <Collapse
              className="justify-content-end"
              isOpen={collapseOpen}
              navbar
            >
              <Nav navbar>
                <NavItem>
                  <NavLink to="/index" tag={Link} style={{ color: "black" }}>
                    Home
                  </NavLink>
                </NavItem>
                <NavItem></NavItem>
                <NavItem>
                  <NavLink
                    to="/landing-page"
                    tag={Link}
                    style={{ color: "black" }}
                  >
                    About Us
                  </NavLink>
                </NavItem>
                <UncontrolledDropdown nav style={{ color: "black" }}>
                  <DropdownToggle
                    caret
                    color="default"
                    href="#pablo"
                    nav
                    onClick={e => e.preventDefault()}
                    style={{ color: "black" }}
                  >
                    <p>Connect</p>
                  </DropdownToggle>
                  <DropdownMenu>
                    <DropdownItem to="parent-list" tag={Link}>
                      <i className="now-ui-icons business_chart-pie-36 mr-1"></i>
                      User List
                    </DropdownItem>
                    <DropdownItem href="child-list">
                      <i className="now-ui-icons design_bullet-list-67 mr-1"></i>
                      Driver List
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
                <NavItem>
                  <NavLink
                    onClick={props.signOut}
                    tag={Link}
                    style={{ color: "black" }}
                  >
                    Log Out
                  </NavLink>
                </NavItem>
              </Nav>
            </Collapse>
          </Container>
        </Navbar>
      </>
    );
  }
  if (auth.uid && auth.email != "admin@admin.com") {
    console.log(props.signOut());
    return (
      <>
        {collapseOpen ? (
          <div
            id="bodyClick"
            onClick={() => {
              document.documentElement.classList.toggle("nav-open");
              setCollapseOpen(false);
            }}
          />
        ) : null}
        <Navbar
          className={"fixed-top " + navbarColor}
          expand="lg"
          color="danger"
        >
          <Container>
            <div className="navbar-translate">
              <NavbarBrand id="navbar-brand" style={{ color: "black" }}>
                PROTECHT
              </NavbarBrand>
              <button
                className="navbar-toggler navbar-toggler"
                onClick={() => {
                  document.documentElement.classList.toggle("nav-open");
                  setCollapseOpen(!collapseOpen);
                }}
                aria-expanded={collapseOpen}
                type="button"
              >
                <span className="navbar-toggler-bar top-bar"></span>
                <span className="navbar-toggler-bar middle-bar"></span>
                <span className="navbar-toggler-bar bottom-bar"></span>
              </button>
            </div>
            <Collapse
              className="justify-content-end"
              isOpen={collapseOpen}
              navbar
            >
              <Nav navbar>
                <NavItem>
                  <NavLink to="/index" tag={Link}>
                    Home
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/login-page" tag={Link}>
                    Login
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/landing-page" tag={Link}>
                    About Us
                  </NavLink>
                </NavItem>

                <NavItem>
                  <Button
                    className="nav-link btn-neutral"
                    color="red"
                    href="#pablo"
                    id="upgrade-to-pro"
                    onClick={e => e.preventDefault()}
                  >
                    <i className="now-ui-icons arrows-1_share-66 mr-1"></i>
                    <p>Sign Up</p>
                  </Button>
                  <UncontrolledTooltip target="#upgrade-to-pro">
                    Download The App!
                  </UncontrolledTooltip>
                </NavItem>
              </Nav>
            </Collapse>
          </Container>
        </Navbar>
      </>
    );
  } else {
    return (
      <>
        {collapseOpen ? (
          <div
            id="bodyClick"
            onClick={() => {
              document.documentElement.classList.toggle("nav-open");
              setCollapseOpen(false);
            }}
          />
        ) : null}
        <Navbar
          className={"fixed-top " + navbarColor}
          expand="lg"
          color="danger"
        >
          <Container>
            <div className="navbar-translate">
              <NavbarBrand id="navbar-brand">PROTECHT</NavbarBrand>
              <button
                className="navbar-toggler navbar-toggler"
                onClick={() => {
                  document.documentElement.classList.toggle("nav-open");
                  setCollapseOpen(!collapseOpen);
                }}
                aria-expanded={collapseOpen}
                type="button"
              >
                <span className="navbar-toggler-bar top-bar"></span>
                <span className="navbar-toggler-bar middle-bar"></span>
                <span className="navbar-toggler-bar bottom-bar"></span>
              </button>
            </div>
            <Collapse
              className="justify-content-end"
              isOpen={collapseOpen}
              navbar
            >
              <Nav navbar>
                <NavItem>
                  <NavLink to="/index" tag={Link}>
                    Home
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/login-page" tag={Link}>
                    Login
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink to="/landing-page" tag={Link}>
                    About Us
                  </NavLink>
                </NavItem>

                <NavItem>
                  <Button
                    className="nav-link btn-neutral"
                    color="red"
                    href="#pablo"
                    id="upgrade-to-pro"
                    onClick={e => e.preventDefault()}
                  >
                    <i className="now-ui-icons arrows-1_share-66 mr-1"></i>
                    <p>Sign Up</p>
                  </Button>
                  <UncontrolledTooltip target="#upgrade-to-pro">
                    Download The App!
                  </UncontrolledTooltip>
                </NavItem>
              </Nav>
            </Collapse>
          </Container>
        </Navbar>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.firebase.auth
  };
};

const mapDispatchToProps = dispatch => {
  return {
    signOut: () => dispatch(signOut())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(IndexNavbar);
